// @dynamic

export class AOString {

  static onlyNumbers(text): string {
    return text.toString().replace(/\D/g, '');
  }

  static trim(text): string {
    return text.toString().replace(/\s+/g, ' ').trim();
  }

  static readingTime(text: string) {
    return text.length * 0.2 * 1000;
  }

  static slug(text): string {
    return text.toString().toLowerCase()
      .replace(/[]+/g, 'a')   // Special Characters #1
      .replace(/[]+/g, 'e')       // Special Characters #2
      .replace(/[]+/g, 'i')       // Special Characters #3
      .replace(/[]+/g, 'o')    // Special Characters #4
      .replace(/[]+/g, 'u')       // Special Characters #5
      .replace(/[]+/g, 'y')       		// Special Characters #6
      .replace(/[]+/g, 'n')       			// Special Characters #7
      .replace(/[]+/g, 'c')       			// Special Characters #8
      .replace(/[]+/g, 'ss')       			// Special Characters #9
      .replace(/[]+/g, 'ae')       			// Special Characters #10
      .replace(/[]+/g, 'oe')       		// Special Characters #11
      .replace(/[%]+/g, 'pct')       			// Special Characters #12
      .replace(/\s+/g, '-')           		// Replace spaces with -
      .replace(/[^\w\-]+/g, '')       		// Remove all non-word chars
      .replace(/\-\-+/g, '-')         		// Replace multiple - with single -
      .replace(/^-+/, '')             		// Trim - from start of text
      .replace(/-+$/, '');            		// Trim - from end of text;
  }

  static keywords(text: string | string[]): string[] {

    if (Array.isArray(text)) {
      text = text.join(' ');
    }

    return AOString.trim(text.toString())
      .split(' ')
      .map(part => AOString.slug(part))
      .reduce((acc, val) => acc.concat([val].concat(val.split('-'))), [])
      .filter((value, index, self) => {
        return self.indexOf(value) === index;
      });
  }

}
